// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import wx from 'weixin-js-sdk';
import axios from 'axios'
Vue.config.productionTip = false

Vue.prototype.wxShare = (obj)=>{
  axios.get('/ykb/wp/public/config/?url='+window.location.href)
    .then(res=>{
      if(res.data.code === 2000){
        let data = res.data.result
        wx.config({
          debug: false,
          appId: data.appId,
          timestamp: data.timestamp,
          nonceStr: data.nonceStr,
          signature: data.signature,
          jsApiList: [
            'onMenuShareTimeline',
            'onMenuShareAppMessage'
          ]
        })
      }else{
        console.log('')
      }
    })
    .catch(error=>{
      console.log(error)
    });
  wx.ready(()=>{
    // 
    wx.onMenuShareAppMessage({
      title: obj.title, // 
      desc: obj.desc, // 
      link: obj.link, // 
      imgUrl: obj.imgUrl, // 
      type: '', // ,musicvideolinklink
      dataUrl: '', // typemusicvideo
      success: function () {
        console.log('');
        if(obj.hasOwnProperty('success') && typeof obj.success === 'function'){
          obj.success()
        }
      },
      cancel: function () {
        // 
        console.log('')
        if(obj.hasOwnProperty('cancel') && typeof obj.cancel === 'function'){
          obj.cancel()
        }
      },
      fail: function (res) {
        console.log(JSON.stringify(res));
        if(obj.hasOwnProperty('fail') && typeof obj.fail === 'function'){
          obj.fail()
        }
      }
    });

    // 
    wx.onMenuShareTimeline({
      title: obj.title, // 
      link: obj.link,
      imgUrl: obj.imgUrl, // 
      success: function () {
        console.log('');
        if(obj.hasOwnProperty('success') && typeof obj.success === 'function'){
          obj.success()
        }
      },
      cancel: function () {
        // 
        console.log('')
        if(obj.hasOwnProperty('cancel') && typeof obj.cancel === 'function'){
          obj.cancel()
        }
      },
      fail: function (res) {
        console.log(JSON.stringify(res));
        if(obj.hasOwnProperty('fail') && typeof obj.fail === 'function'){
          obj.fail()
        }
      }
    });
  })
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



